h2 {
  display: inline-block;
}
label.input {
  display: inline-block;
  width: 100px;
  text-align: right;
}

input {
  width: 200px;
}

video,
canvas,
#cloudinary_photo {
  border: 1px solid black;
  background-color: #efefef;
}

#camera_upload > p {
  font-size: 0.9rem;
  color: #333;
  margin: 0px 0px 0px 105px;
}

#uploadBtn {
  margin: 20px 0 0 105px;
}

#upload_response {
  border: 1px solid black;
  width: 100%;
  min-height: 100px;
  white-space: pre;
  padding: 10px;
  box-sizing: border-box;
}
